* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
body{
    background-color: #182f35;
}

.login-container-form {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: auto;
    width: 40%;
    box-shadow: 0px 0px 10px rgb(143, 143, 143);
    height: auto;
    padding: 1.5rem;
    background-color: #ffffff;
    border-radius: 5px;
}

.input-check-btn {
    display: flex;
    gap: 1rem;
    align-items: center;
    margin: 1rem;
    justify-content:center
}

.Login-btn {
    background-color: #3b82f6;
    width: 100%;
    height: 4rem;
    border-radius: 5px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: .4rem;
    color: #ebebeb;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
    font-size: 1.3rem;
    margin-top: 1rem;
}

.label {
    font-weight: 500;
    display: flex;
    justify-content: flex-start;
    margin-top: 2rem;

}

.login-form-header {
    display: flex;
    justify-content: center;
    color: #444444;
}

.input-lg {
    border: none;
    padding: .5rem;
    align-items: center;
    display: flex;
    border: 1px solid rgb(184, 184, 184);
    margin-top: .2rem;
    border-radius: 4px;

}

.login-create-acc {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
}

.container-login {
    width: 100%;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar-login-btn a {
    text-decoration: none;

}